import { SchemeName } from '@backbase/data-ang/stop-checks';
import { RequestStatus } from '../store/store';

export interface StopCheckRequestInfo {
  payFromAccount: {
    id: string;
    name: string;
    currency?: string;
    balance?: number;
    accountNumber: string;
    accountType: SchemeName;
  };
  checkNumber: string;
  firstCheckNumber: string;
  lastCheckNumber: string;
  stopRangeType: string;
  amountGroup?: {
    amount: string;
    currency: string;
  };
  payeeName?: string;
  dateIssued?: string;
  expirationDate?: string;
  stopReason?: string;
  otherReason?: string;
}

export interface InitialState {
  requestInfo?: StopCheckRequestInfo;
  status?: RequestStatus;
}

export interface ActionPayload {
  type: string;
  payload?: InitialState;
}
